.product {
  transition: all 0.3s;

  @include for-desktop() {
    width: 324px;
  }

  &__img {
    width: 100%;
    object-fit: contain;
    padding-bottom: 32px;
  }

  &__name {
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: 140%; /* 22.4px */
    padding-bottom: 8px;
  }

  &__info {
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%; /* 22.4px */
    padding-bottom: 12px;
  }

  &__price {
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: 140%; /* 22.4px */
  }

  &:hover {
    transform: scale(0.95);

    @include for-desktop() {
      transform: scale(1.2);
    }
  }
}
